
import {Box, makeStyles, Typography, Button} from '@material-ui/core';
import { useHistory } from 'react-router-dom';



const useStyle = makeStyles(theme=>({
    component:{
        margin: '55px 140px',
        width: '80%', 
        background:'#fff',
        height: '65vh',  
        [theme.breakpoints.down('md')]:{
            margin:'55px 50px',
        }
    },
    image:{
        width: '15%',
    },
    container:{
         
        textAlign: 'center',
        paddingTop: '70px',
        '& > *':{
            marginTop: 10,
            fontSize: 14,
        }

    },
    button:{
        marginTop: 20,
        padding: '12px 70px',
        backgroundColor: '#2874f0',
        borderRadius: 2,
        fontSize: 14,
        color: '#fff',
    }
}))

const EmptyCart = ()=>{

    const classes = useStyle();
    const emptycarturl = 'https://rukminim1.flixcart.com/www/800/800/promos/16/05/2019/d438a32e-765a-4d8b-b4a6-520b560971e8.png?q=90';
    
    const history = useHistory();
    
    const addItem = ()=>{
        history.push('/');
    }
    
    return (
        
        <Box className={classes.component}>
            <Box className={classes.container} >
            <img src={emptycarturl} className={classes.image} />
            <Typography>Your Cart Is Empty!</Typography>
            <Typography>Add Items To It Now.</Typography>
            <Button className={classes.button} variant='contained'  onClick={()=>addItem()}>Shop Now</Button>
            </Box>
            
        </Box>
        
    )
}

export default EmptyCart;